const {Parser, AsyncParser} = require('json2csv');
const FileSaver = require('file-saver');

export const csvHelper = {
    csvPrefix: "\uFEFF", // 
    /**
     * jsoncsv
     * @param data 
     * @param fields 
     * @param extra  unwind
     */
    asCsv(data, fields, ...extra) {
        const unwind = extra[0] ? extra[0] : null;
        const parser = new Parser({fields, unwind, withBOM: true});
        return parser.parse(data);
    },
    asCsvAsync(callback, data, fields, ...extra) {
        const unwind = extra[0] ? extra[0] : null;
        let csv = "\uFEFF";
        const asyncParser = new AsyncParser({fields, unwind}, {encoding: 'utf8', highWaterMark: 8092});
        asyncParser.processor
            .on('data', chunk => (csv += chunk))
            .on('end', () => callback(csv));
        asyncParser.input.push(JSON.stringify(data));
        asyncParser.input.push(null);
    },
    /**
     * csv
     * @param name   .csv
     * @param data  json 
     * @param fields 
     * @param extra  asCsv
     */
    exports(name, data, fields, ...extra) {
        const csv1 = csvHelper.asCsv(data, fields, ...extra);
        csvHelper.asCsvAsync(csv => {
            console.log(csv1);
            const blob = new Blob([csv]);
            FileSaver.saveAs(blob, `${name}.csv`);
        }, data, fields, ...extra);
    },
};
